/* fonts */

@import url('https://fonts.googleapis.com/css2?family=Bungee&display=swap');


/* general styling */

#align {width: 80%;
margin-left:auto; 
margin-right:auto;
font-family: 'Bungee', cursive,"Trebuchet MS", Arial, Helvetica, sans-serif; 
}

/* navbar */

.navimg {  
  position: relative;
  top: 13px;  
}

ul.topnav {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: black;  
  border-bottom: 1px solid ;
  border-bottom-color: #eda338;
  font-family: 'Bungee', cursive,"Trebuchet MS", Arial, Helvetica, sans-serif;  
}

ul.topnav li {
  float: left;   
}

ul.topnav li a {
  display: block;
  color: #FFFFFF !important;  
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  -webkit-font-smoothing: antialiased !important;
	-moz-osx-font-smoothing: grayscale !important;
}

ul.topnav li a.selected {
  display: block;
  color: #FFFFFF !important;
  background-color: #eda338;  
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  -webkit-font-smoothing: antialiased !important;
	-moz-osx-font-smoothing: grayscale !important;
}

ul.topnav li a:hover {
  background-color: #E7205F;
  text-decoration: none;
  color: white;
  transition: all 0.15s ease;
}

ul.topnav li.navright {
  float: right;
  background-color: #03A19A;
}

@media screen and (max-width: 560px) {
  ul.topnav li.navright, 
  ul.topnav li {float: none;}
  .navimg {display: none;}
 
}


/* transitions */

.fade-enter {
  opacity: 0.01;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 150ms ease-in;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active{
  opacity: 0.01;
  transition: opacity 0ms ease-out;
}

div.transition-group {
  position: relative;

}

section.route-section {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
}

/* cards */

.contactCards{
  width: 80%;
  margin-left:auto; 
  margin-right:auto;
}


/* general */

#flex{
  display: flex;
}

body{
  height: 100%;
  margin: 0;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-image: linear-gradient(black, black, #322f31);
  color: #FFFFFF !important; 
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
  text-rendering: optimizeLegibility;
	-webkit-font-feature-settings: "liga", "kern";
}

a{
  color: #eda338 !important;
}

.heading{
  color: #FFFFFF !important;
  font-weight: 500;  
  text-shadow: -2px 1px rgba(black, 0.1);
  -webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
  
}

.card{
  background-color: #322f31 !important;
  z-index: 100;
  -webkit-font-smoothing: antialiased !important;
	-moz-osx-font-smoothing: grayscale !important;
}

.card:hover{
  background-color: #424042 !important;
  transition: all 0.3s ease;
}

/* navbar */

.navbar {
  background-color: black !important;
  list-style-type: none;
  border-bottom: 1px solid ;
  border-bottom-color: #eda338;
  margin: 0 !important;
  padding: 0px !important;
  overflow: hidden;
  font-family: "Bungee", cursive,"Trebuchet MS", Arial, Helvetica, sans-serif;  

}
